cess to ways sustainability can be integrated in curriculum, potential help for doing so, and the student interest in sustainability topics and delivery methods. ")]),_c('p',[_vm._v(" To read more about our project visit see (link will be inserted once our report is published). ")])])]),_c('div',{staticClass:"container"},[_c('img',{staticClass:"container__imageR",attrs:{"src":require("../assets/sdglogo.png")}}),_c('div',{staticClass:"container__text"},[_c('h1',[_vm._v("About the Sustainable Development Goals")]),_c('p',[_vm._v(" We chose to categorize the sustainability themed courses further according to the United Nations’ Sustainable Development Goals. The Sustainable Development Goals are 17 goals that were announced as part of the 2030 Agenda for Sustainable Development. We chose to use these goals as categories for mapping sustainability because these goals encompass a wide array of topics. We believe that each sustainability themed course should relate in some way to one or more SDG. WPI's Office of Sustainability has also expressed interest in promoting the Sustainable Development Goals in curricula. ")]),_c('p',[_vm._v(" To read more about the Sustainable Development Goals, visit the "),_c('a',{attrs:{"href":"https://sustainabledevelopment.un.org/"}},[_vm._v("Sustainable Development Goals Knowledge Platform.")])])])]),_c('div',{staticClass:"container"},[_c('img',{staticClass:"container__imageL",attrs:{"src":require("../assets/team.png")}}),_c('div',{staticClass:"container__text"},[_c('h1',[_vm._v("About Us")]),_c('p',[_vm._v(" Our team is composed of three WPI undergraduate students: Emily Baker, Rui Huang and Akash Shaji. We completed this project in order to fulfill our Interactive Qualifying Project Requirement (IQP). Our team was scheduled to complete our IQP at the University of Worcester in Worcester, UK, but due to COVID-19 we were required to do our project remotely instead. This still ended up being a rewarding experience as we got to spend this te